html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

/*-----header----------*/
.header {
  padding: 25px 35px;
  box-shadow: 0 2px 9px rgba(189, 189, 189, 0.5);
  position: absolute;
  width: 100%;
  box-sizing: border-box;
  background-color: #fff;
}

.header__logo {
  display: inline-block;
  margin-right: 60px;
}

.header__menu-bars {
  display: none;
}

.header__nav {
  display: inline-block;
  position: absolute;
  top: 50%;
  margin-top: -9px;
}

@media only screen and (max-width: 769px) {
  .header__nav {
    display: none;
    position: relative;
    margin-top: 5px;
    text-align: center;
  }
}

.header__list_item {
  display: inline-block;
  margin-right: 60px;
}

@media only screen and (max-width: 993px) {
  .header__list_item {
    margin-right: 30px;
  }
}

@media only screen and (max-width: 769px) {
  .header__list_item {
    display: block;
    margin-top: 15px;
    margin-right: 0px;
  }
}

.header__list_link {
  text-decoration: none;
  text-transform: uppercase;
  color: #9197a3;
  font-family: "PT Sans", sans-serif;
  font-size: 11px;
  font-weight: bold;
}

.header__list_link:hover {
  color: #333743;
}

@media only screen and (max-width: 769px) {
  .header__list_link {
    font-size: 16px;
  }
}

.header__account_link {
  opacity: 1;
  color: #333743;
  font-family: "PT Sans", sans-serif;
  font-size: 13px;
  font-weight: bold;
  text-decoration: none;
}

.header__account_link:hover {
  opacity: .7;
}

@media only screen and (max-width: 769px) {
  .header__account_link {
    font-size: 16px;
  }
}

.header__account {
  float: right;
  margin-top: 5px;
}

@media only screen and (max-width: 769px) {
  .header__account {
    display: none;
    float: none;
    text-align: center;
    margin-top: 15px;
  }
}

.header__account-bag {
  display: inline-block;
  margin-right: 30px;
}

@media only screen and (max-width: 769px) {
  .header__account-bag {
    display: block;
    margin-right: 0px;
    margin-left: 42px;
  }
}

.header__account-bag_index {
  text-decoration: none;
  color: #fff;
  border: 1px solid transparent;
  padding: 5px 8px;
  border-radius: 25px;
  background-color: #333743;
  font-family: "Georgia", sans-serif;
  font-size: 11px;
  margin-left: 15px;
  display: inline-block;
  height: 25px;
  width: 25px;
  box-sizing: border-box;
}

.header__account-registration {
  display: inline-block;
  margin-left: 15px;
}

@media only screen and (max-width: 769px) {
  .header__account-registration {
    display: block;
    margin-left: 0px;
  }
}

.header__account_list {
  display: inline-block;
  padding-left: 15px;
}

@media only screen and (max-width: 769px) {
  .header__account_list {
    display: block;
    padding-left: 0px;
    margin-top: 15px;
  }
}

.header__account_list-border:after {
  content: "|";
  font-size: 11px;
  background-color: #000;
  margin-left: 15px;
}

@media only screen and (max-width: 769px) {
  .header__account_list-border:after {
    content: none;
  }
}

.header__account_line {
  display: inline-block;
  border-right: 1px solid #ddd;
  height: 85px;
  position: absolute;
  top: 0px;
}

@media only screen and (max-width: 769px) {
  .header__account_line {
    display: none;
  }
}

.header__menu-bars {
  width: 35px;
  height: 35px;
  position: relative;
  cursor: pointer;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
}

@media only screen and (max-width: 769px) {
  .header__menu-bars {
    display: inline-block;
    position: absolute;
    top: 25px;
    right: 35px;
  }
}

.header__menu-bars span {
  display: block;
  position: absolute;
  height: 5px;
  width: 100%;
  background: #000;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.25s ease-in-out;
  -moz-transition: 0.25s ease-in-out;
  -o-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}

.header__menu-bars span:nth-child(1) {
  top: 0px;
}

.header__menu-bars span:nth-child(2) {
  top: 13px;
}

.header__menu-bars span:nth-child(3) {
  top: 13px;
}

.header__menu-bars span:nth-child(4) {
  top: 26px;
}

.header__menu-bars.open span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%;
}

.header__menu-bars.open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

.header__menu-bars.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.header__menu-bars.open span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%;
}

@media only screen and (max-width: 769px) {
  .responsive {
    display: block;
  }
}

/*----- header-end ----------*/
/*-----product----------*/
.clearfix:after {
  content: '';
  clear: both;
  display: table;
}

.container {
  width: 1200px;
  margin: 0 auto;
}

@media only screen and (max-width: 1200px) {
  .container {
    width: 970px;
  }
}

@media only screen and (max-width: 993px) {
  .container {
    width: 750px;
  }
}

@media only screen and (max-width: 769px) {
  .container {
    width: 100%;
  }
}

.product {
  padding-top: 135px;
}

@media only screen and (max-width: 993px) {
  .product {
    text-align: center;
  }
}

.product__gallery, .product__description {
  display: inline-block;
  width: 50%;
}

@media only screen and (max-width: 1200px) {
  .product__gallery, .product__description {
    text-align: center;
  }
}

@media only screen and (max-width: 993px) {
  .product__gallery, .product__description {
    width: 80%;
  }
}

.product__gallery_small-img {
  display: inline-block;
  vertical-align: top;
  margin-right: 16px;
  float: left;
}

@media only screen and (max-width: 1200px) {
  .product__gallery_small-img {
    float: none;
    margin-top: 15px;
    margin-right: 0px;
  }
}

.product__gallery_img {
  display: block;
  margin-bottom: 12px;
}

@media only screen and (max-width: 1200px) {
  .product__gallery_img {
    display: inline-block;
    margin-right: 15px;
  }
}

.product__description {
  float: right;
  text-align: center;
  vertical-align: top;
}

@media only screen and (max-width: 993px) {
  .product__description {
    float: none;
  }
}

.product__chapter {
  font-family: "Georgia", sans-serif;
  font-size: 12px;
  color: #9fcddd;
  margin: 20px 0px;
  display: block;
}

.product__chapter_link {
  text-decoration: none;
  color: #9fcddd;
}

.product__title {
  font-family: "PT Sans", sans-serif;
  font-weight: bold;
  font-size: 24px;
  width: 60%;
  margin: auto;
  line-height: 1.5;
  text-transform: uppercase;
}

@media only screen and (max-width: 480px) {
  .product__title {
    width: 90%;
  }
}

.product__chapter_mars {
  margin-right: 7px;
}

.product__chapter_angel {
  margin: 0px 5px;
}

.product__price {
  font-family: "Georgia", sans-serif;
  font-size: 22px;
  display: block;
  margin: 60px 0px;
}

.product__description_title {
  font-family: "PT Sans", sans-serif;
  font-weight: bold;
  font-size: 13px;
  border-bottom: 1px solid #dbdde5;
  padding-bottom: 10px;
}

.product__description_text {
  color: #9197a3;
  font-family: "Georgia", sans-serif;
  font-size: 12px;
  line-height: 1.5;
  width: 80%;
  margin: auto;
  margin-top: 20px;
}

.product__colors_not-check {
  margin-right: 15px;
}

.product__colors_check {
  display: none;
  margin-right: 15px;
}

input[type=radio] {
  display: none;
}

input[type=radio]:checked + label .product__colors_check {
  display: inline-block;
  color: #333743;
}

input[type=radio]:checked + label .product__colors_not-check {
  display: none;
}

.fa-stop_blue {
  color: #9fcede;
  margin-right: 10px;
}

.fa-stop_brown {
  color: #333743;
  margin-right: 10px;
}

.product__colors {
  margin: 50px 0px 55px 50px;
}

@media only screen and (max-width: 769px) {
  .product__colors {
    margin: 50px 0px 75px 0px;
  }
}

.product__colors_choice {
  display: block;
  text-align: left;
  color: #9197a3;
  font-family: "Georgia", sans-serif;
  font-size: 13px;
  margin-bottom: 17px;
}

@media only screen and (max-width: 993px) {
  .product__colors_choice {
    display: inline-block;
  }
}

.product__colors_choice_blue {
  color: #333743;
}

@media only screen and (max-width: 993px) {
  .product__colors_choice_blue {
    margin-right: 100px;
  }
}

@media only screen and (max-width: 480px) {
  .product__colors_choice_blue {
    margin-right: 20px;
  }
}

.product__colors_title {
  color: #333743;
  font-family: "PT Sans", sans-serif;
  font-size: 13px;
  font-weight: bold;
  text-align: left;
  margin-left: 25px;
  margin-bottom: 15px;
  border-bottom: 1px solid #dbdde5;
  padding-bottom: 10px;
  width: 30%;
}

@media only screen and (max-width: 993px) {
  .product__colors_title {
    width: 90%;
    text-align: center;
  }
}

@media only screen and (min-width: 769px) {
  .product__colors_title {
    margin-left: 0px;
  }
}

@media only screen and (max-width: 480px) {
  .product__colors_title {
    width: 100%;
    margin-left: 0px;
  }
}

.product__button {
  text-decoration: none;
  color: #fff;
  background-color: #afa394;
  font-family: "Georgia", sans-serif;
  font-size: 13px;
  border-radius: 2px;
  padding: 20px 180px;
  display: inline-block;
}

.product__button:hover {
  opacity: 0.9;
}

@media only screen and (max-width: 769px) {
  .product__button {
    padding: 20px 140px;
  }
}

@media only screen and (max-width: 480px) {
  .product__button {
    padding: 20px 79px;
  }
}

@media only screen and (max-width: 769px) {
  .product__gallery_main-img {
    width: 400px;
  }
}

@media only screen and (max-width: 480px) {
  .product__gallery_main-img {
    width: 100%;
  }
}

/*----- product-end ---------*/
/*-----footer----------*/
.footer {
  margin: 120px 0px 50px 0px;
}

.footer_wrapper {
  text-align: center;
}

.footer__text {
  color: #9197a3;
  font-family: "Georgia", sans-serif;
  font-size: 11px;
  margin-bottom: 10px;
  text-align: center;
}

.footer__title {
  color: #333743;
  font-family: "PT Sans", sans-serif;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 22px;
  text-align: center;
}

.footer__item {
  display: inline-block;
  margin-right: 10px;
  margin-bottom: 20px;
  width: 226px;
}

.footer__item_link {
  text-decoration: none;
}

.footer__item_img {
  margin-bottom: 17px;
  width: 100%;
}

.footer__item_description {
  padding: 0px 10px;
  text-align: left;
}

.footer__item_name {
  display: inline-block;
  text-transform: uppercase;
  color: #333743;
  font-family: "PT Sans", sans-serif;
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 5px;
}

.footer__item_price {
  color: #9197a3;
  font-family: "Georgia", sans-serif;
  font-size: 13px;
  float: right;
}

.footer__item_category {
  display: block;
  color: #9197a3;
  font-family: "Georgia", sans-serif;
  font-size: 11px;
}

/*----- footer-end ---------*/
